import Phaser from 'phaser';

import { Scene } from 'phaser';

class TutorialScene extends Scene{
    constructor(){
      super({ key: 'TutorialScene'})
    };
    preload(){
        this.load.image('bg', '../assets/lvl3/bg.png');
        this.load.image('ground', '../assets/lvl3/ground.png');
        this.load.spritesheet('person', '../assets/lvl3/person.png', { frameWidth: 32, frameHeight: 48 });
    };
    create(){
        this.add.image(0, 0, 'bg').setOrigin(0);
        this.platforms = this.physics.add.staticGroup();
        this.platforms.create(0, 550, 'ground').setOrigin(0).refreshBody();

        this.player = this.physics.add.sprite(100, 450, 'person');

        this.player.setBounce(0.2);
        this.player.setCollideWorldBounds(true);

        this.physics.add.collider(this.player, this.platforms);

        this.cursors = this.input.keyboard.createCursorKeys()
    };

    update(){
        if (this.cursors.left.isDown)
        {
            this.player.setVelocityX(-160);
        }
        else if (this.cursors.right.isDown)
        {
            this.player.setVelocityX(160);
        }
        else
        {
            this.player.setVelocityX(0);
        }

        if (this.cursors.up.isDown && this.player.body.touching.down)
        {
            this.player.setVelocityY(-330);
        }
    };
}

function launch(containerId) {
    return new Phaser.Game({
      type: Phaser.AUTO,
      width: 900,
      height: 600,
      parent: containerId,
      physics: {
        default: 'arcade',
        arcade: {
            gravity: { y: 300 },
            debug: false
        }
      },
      scene: [TutorialScene]
    })
  }
  
  export default launch
  export { launch }